import uniCard from "@/components/uni-card/uni-card.vue";
import api from "@/common/index.js";
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
import '@/common/uni.css';
import uCharts from '@/components/u-charts/u-charts.js';
var _self;
var canvaLineA = null;
var mvc = {
	num: '',
	isShow: false,
	hide: false,
	all: [],
	name: '',

}
export default {
	components: {
		uniNavBar
	},
	data() {
		const currentDate1 = this.getDate({
			format: true
		})
		const currentDate2 = this.postDate({
			format: true
		})
		return {
			mvc: {
				num: '',
				isShow: false,
				hide: false,
				all: [],
				name: '',
				cWidth: '',
				cHeight: '',
				pixelRatio: 1,
				itemCount: 20, //x轴单屏数据密度
				sliderMax: 50,
				showLine: 0
			},
			dateStart: currentDate1,
			dateEnd: currentDate2,
		};
	},
	onLoad() {
		let that = this;
		setTimeout(function() {
			mvc = that.mvc;
			api.pageTitle(that.$t('market').title)
			const params = uni.getStorageSync('userinfo');
			mvc.num = params.hdid;
			_self = that;











			mvc.cWidth = uni.upx2px(750);
			mvc.cHeight = uni.upx2px(500);
			that.mvc = mvc;
		}, 300);

	},
	computed: {
		i18n() {
			return this.$t('market')
		},
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.postDate('end');
		}
	},
	methods: {

		showLineA(canvasId, chartData) {

			canvaLineA = new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'line',
				padding: [15, 15, 0, 15],
				legend: {
					show: true,
					padding: 5,
					lineHeight: 11,
					margin: 0,
				},
				fontSize: 11,
				dataLabel: true,
				dataPointShape: true,
				background: '#FFFFFF',
				pixelRatio: mvc.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: true,
				enableScroll: true, //开启图表拖拽功能
				xAxis: {
					disableGrid: false,
					type: 'grid',
					gridType: 'dash',
					itemCount: 4, //可不填写，配合enableScroll图表拖拽功能使用，x轴单屏显示数据的数量，默认为5个
					scrollShow: true, //新增是否显示滚动条，默认false
					scrollAlign: 'left',
					dashLength: 8
					//scrollBackgroundColor:'#F7F7FF',//可不填写，配合enableScroll图表拖拽功能使用，X轴滚动条背景颜色,默认为 #EFEBEF
					//scrollColor:'#DEE7F7',//可不填写，配合enableScroll图表拖拽功能使用，X轴滚动条颜色,默认为 #A6A6A6
				},
				yAxis: {
					//disabled:true
					gridType: 'dash',
					splitNumber: 8,
					min: 10,
					max: 100,
					format: (val) => {
						return val.toFixed(0)
					} //如不写此方法，Y轴刻度默认保留两位小数
				},
				width: mvc.cWidth * mvc.pixelRatio,
				height: mvc.cHeight * mvc.pixelRatio,

				extra: {
					lineStyle: 'straight'
				},

			});
			uni.hideLoading();
		},
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		bindBeginTime: function(e) {
			this.dateStart = e.target.value
		},
		bindExpireTime: function(e) {
			this.dateEnd = e.target.value
		},
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			mvc[key] = e.detail.value;
			this.mvc = mvc
		},

		getDate(type) {
			// 获取前天时间
			let inputTime = new Date();
			const date = new Date(inputTime);
			let year = date.getFullYear();
			let month = date.getMonth();
			let day = date.getDate();
			if (type == 'start') {
				year = year - 60;

			} else if (type == 'end') {
				year = year + 2;
			}
			if (month == 0) {
				year = parseInt(year) - 1;
				month = 12;
			};
			if (month > 12) {
				year = parseInt(year);
				month = 1;
			};
			month = month > 9 ? month : '0' + month;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;


		},
		postDate(type) {
			// 获取昨天时间
			let inputTime = new Date();
			// let inputTime = new Date(new Date - 1000 * 60 * 60 * 24);
			const date = new Date(inputTime);
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();
			if (type == 'start') {
				year = year - 60;

			} else if (type == 'end') {
				year = year + 2;
			}
			if (month == 0) {
				year = parseInt(year) - 1;
				month = 12;
			};
			if (month > 12) {
				year = parseInt(year) + 1;
				month = 1;
			};
			month = month > 9 ? month : '0' + month;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},

		search() {
			uni.showLoading({
				title: this.$t('market').loading
			});
			let that = this;
			that.all = [];
			that.name = "";
			mvc.isShow = false;
			mvc.hdid = false;
			let params = {
				sdate: this.dateStart,
				edate: this.dateEnd
			}
			this.postsale(params);

		},
		postsale(params) {
			let that = this;
			that.$ajax.marketRank(params).then(res => {
				if (res == null || res.length == 0) {
					mvc.hide = true;
					uni.hideLoading();
					return false;
				};

				let LineA = {
					categories: [],
					series: []
				};
				let arry = {
					name: this.$t('market').num,
					data: []
				}
				let money = {
					name: this.$t('market').money,
					data: []
				}
				for (let i in res.num) {
					let title = "";

					switch (i) {
						case '01':
							title = this.$t('market').category[0];
							break;
						case '02':
							title = this.$t('market').category[1];
							break;
						case '03':
							title = this.$t('market').category[2];
							break;
						case '04':
							title = this.$t('market').category[3];
							break;
						case '05':
							title = this.$t('market').category[4];
							break;
						case '06':
							title = this.$t('market').category[5];
							break;
						case '07':
							title = this.$t('market').category[6];
							break;
						case '08':
							title = this.$t('market').category[7];
							break;
						case '09':
							title = this.$t('market').category[8];
							break;
						case '10':
							title = this.$t('market').category[9];
							break;
						case '14':
							title = this.$t('market').category[10];

					}

					LineA.categories.push(title)
					arry.data.push(res.num[i])

				}

				for (let k in res.money) {
					money.data.push(res.money[k]);
				}

				LineA.series.push(arry);
				LineA.series.push(money)
				that.showLineA("canvasLineA", LineA);
				that.getsale(params);
			})
		},
		getsale(params) {
			let that = this;
			that.$ajax.marketView(params).then(res => {
				let list = res;
				if (list.length != 0) {
					mvc.hide = false;
					mvc.all = res[0].zong;
					mvc.name = res[0].NAME;
					mvc.isShow = true;

				} else {
					mvc.hide = true;
					uni.hideLoading();
					return false;
				}
			})
			that.mvc = mvc;
		},
		hidePopup() {
			this.isShow = false;
		},

		touchLineA(e) {


			canvaLineA.scrollStart(e);
		},
		moveLineA(e) {
			canvaLineA.scroll(e);
		},
		touchEndLineA(e) {
			canvaLineA.scrollEnd(e);

			//下面是toolTip事件，如果滚动后不需要显示，可不填写
			canvaLineA.showToolTip(e, {
				format: function(item, category) {
					return category + ' ' + item.name + ':' + item.data
				}
			});

		},
		navdetail(time1, time2) {

			uni.navigateTo({
				url: '/pages/market-detail/market-detail?time1=' + time1 + '&time2=' + time2
			})
		}

	}
}
